@import url('https://fonts.googleapis.com/css2?family=Signika:wght@600&display=swap');

.main__list{
    width: 100%;
    /* height: 100%; */
    font-family: 'Signika', cursive;
    /* border-radius: 0 0 30px 30px; */
    overflow: hidden;
}

.main__list .no__item{
    width: 100%;
    height: 100%;
    display: flex;
    color: tomato;
    justify-content: center;
}

.card__list{
    position: relative;
    padding: 1rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    background: #ffffff;
    transition: .5s;
    width: 100%;
}


.card__{
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    /* justify-content: space-between; */
    align-items: center;
    /* background: tomato; */
}

.card__ .delete__{
    color: rgb(165, 165, 165);
    width: 2rem;
    height: 100%;
    display: none;
    align-items: center;
    justify-content: center;
    padding-right: 1.2rem;
}

.card__ .delete__ .MuiSvgIcon-root{
    font-size: 1.7rem;
}

.card__list:last-child{
    /* margin-bottom: 3.5rem; */
}


.card__list .card__heading{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.card__list .card__heading .card__expands:last-child{
margin-right: 0;
}

.card__list .card__heading .card__expands{
    transition: .5s;
    cursor: pointer;
    background: #ffffff;
box-shadow:  5px 5px 10px #e8e8e8, 
             -5px -5px 10px #ffffff;
             font-size: 1.3rem;
             padding: 0.3rem;
             border-radius: 5px;
             margin-right: 0.5rem;
}

.card__list .card__heading div{
    display: flex;
    align-items: center;
}

.card__list .card__heading .color__heading{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: chocolate;
    margin-right: 0.5rem;
    border: 1px solid rgb(182, 182, 182);
}

.card__list .card__heading .title__heading{
    font-weight: 600;
    letter-spacing: 0.5px;
    font-size: 1.1rem;
}

.card__list:hover{
    cursor: pointer;
}

.card__list .card__body .card__body-items{
    padding: 0.5rem 2.5rem;
    width: 80%;
    transition: .5s;
}

.card__list .card__body{
    padding: 0.5rem 0;
    transition: .5s;
    display: none;
    height: 0;
    overflow: hidden;
}

.card__list .card__body.expands{
    transition: .5s;
    display: block;
    height: 100%;
    animation: 1s anim ease-in-out;
}

.card__list .card__heading .card__expands.active .MuiSvgIcon-root{
    transform: rotate(180deg);
}

.card__list .card__heading .card__expands.active{
    transition: .5s;
}

.card__list .card__heading .card__expands.MuiSvgIcon-root{
    transition: .5s;
}

.card__list .card__body .card__body-items .title {
    font-weight: 600;
    font-size: 0.9rem;
    opacity: 0.5;
}
.card__list .card__body .card__body-items .items {
    font-weight: 600;
    font-size: 1.15rem;
    letter-spacing: 1px;
    margin: 0.35rem 0;
}

.card__list .card__body .card__body-items:hover {
    margin: 0.5rem 0;
    padding: 1rem 2.5rem;
    background: #ffffff;
    box-shadow:  5px 5px 10px #e8e8e8, 
                 -5px -5px 10px #ffffff;
}


@keyframes anim {
    from{
        opacity: 0;
    } 
    to{
        opacity: 1;
    }
}