.video__container {
    /* width: 100%; */
    width: 292px;
    height: 350px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: white;
    overflow: hidden;
}

#video-tracking {
    z-index: -2;
    width: 100%;
    /* width: 292px; */
    height: 350px;
    position: absolute;
}

.main__content{
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.hidden__canvas {
    /* object-fit: fill; */
    z-index: 3;
    /* display: none; */
    /* position: absolute; */
    /* width: 100%; */
    /* width: 292px; */
    height: 350px;
}

.circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    z-index: 5;
    background: rgb(255, 255, 255);
}

.circle__color {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    z-index: 1;
    background: rgb(255, 255, 255);
}

.circle-container {
    position: absolute;
    /* width: 15em; */
    z-index: 5;
    /* height: 15em; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.circle__item {
    opacity: 0;
    border-radius: 50%;
    background-color: deepskyblue;
    width: 4em;
    height: 4em;
    position: absolute;
    animation: scaleIn 4s infinite cubic-bezier(.36, .11, .89, .32);
}

@keyframes scaleIn {
    from {
        transform: scale(.01, .01);
        opacity: .5;
    }
    to {
        transform: scale(3.5, 3.5);
        opacity: 0;
    }
}

.hover__item {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    z-index: 5;
    background-color: rgba(241, 237, 237, 0.747);
    padding: 10px;
    border-radius: 1rem;
    margin-top: -6em;
    /* margin-top: -3em; */
}

.marker {
    transform: perspective(40px) rotateX(20deg) rotateZ(-45deg);
    border-radius: 50% 50% 50% 0;
    padding: 1px;
    width: 50px;
    height: 50px;
    background-color: rgba(241, 237, 237, 0.747);
    position: absolute;
}

.hover__item h5 {
    font-size: 1.25em;
}

.bottom__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    bottom: 0;
    margin-bottom: 2rem;
    /* height: 294px; */
}

.color__list {
    position: relative;
    width: 95%;
    height: 12vh;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 5px 10px;
    padding-top: 0.7rem;
}

.container__color{
    padding-right: 2rem;
    display: flex;
    width: 100%;
}

.main__color{
    padding: 0 5px;
    width: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
}

.main__color:last-child(){
    margin-right: 2rem;
}

.cirle__color {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgb(179, 63, 9);
    display: flex;
    flex-direction: column;
}

.main__color label{
    text-align: center;
    letter-spacing: 0.5px;
    font-size: 0.8rem;
    margin-top: 5px;
    font-weight: 600;
}

.cirle__color .icons {
    cursor: pointer;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    background: rgba(0, 0, 0, 0.705);
    color: rgb(255, 255, 255);
    position: relative;
    z-index: 1;
    border-radius: 50%;
    font-weight: 700;
    font-size: 0.9em;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3px 5px;
    margin-top: -3px;
    margin-left: 15px;
}

.btn__container {
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 0 0 20px 10px;
}

.btn__flip {
    color: rgba(0, 0, 0, 0.705);
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 5em;
    border-radius: 50%;
}

.btn {
    background: #f0f3f4;
    box-shadow: 8px 8px 16px #cccfcf, -8px -8px 16px #ffffff;
    transition: .5s;
}

.btn:hover {
    transform: scale(1.1);
}

.btn__shot {
    transition: .5s;
    width: 80px;
    height: 80px;
    display: flex;
    transition: .5s;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: #a30000;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    color: rgba(255, 255, 255, 0.904);
}

.btn__shot:hover {
    transform: scale(1.09);
    background: #7e0303;
}

#camera {
    width: 50px;
    height: 50px;
}

#icons {
    width: 27px;
    height: 27px;
}