.about__card{
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0.4rem 0;
    transition: .5s;
    text-decoration: none;
    color: black;
    margin-bottom: 1.2rem ;
    animation: 5s opacity;
}

.copyright__{
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    bottom: 40px;
}

.copyright__ h5:nth-child(1){
    letter-spacing: 0.5px;
    font-weight: 500;
}

.copyright__ h5{
    margin-top: 5px;
}

.about__card .icons__social{
    margin-left: 1rem;
    opacity: 0.6;
    display: flex;
    align-items: center;
}

.about__card .icons__social .MuiSvgIcon-root{
    font-size: 2.3rem;
}


.about__card  .container__info{
    margin-left: 0.4rem;
}

.about__card .container__info .title__social{
    font-weight: 700;
    font-size: 1rem;
}

.about__card .container__info .name__social{
    font-weight: 700;
    font-size: 0.8rem;
    opacity: 0.6;
}

@media only screen and (max-width: 540px) {   
    .about__card .icons__social .MuiSvgIcon-root{
        font-size: 3rem;
    }

    .about__card .container__info .title__social{
        font-weight: 700;
        font-size: 1.2rem;
    }
    .about__card .container__info .name__social{
        font-weight: 700;
        font-size: 0.9rem;
        opacity: 0.6;
    }
}


.about__card:hover{
    padding: 1rem 0;
    cursor: pointer;
    background: #ffffff;
    box-shadow:  9px 9px 18px #e8e8e8, 
                 -9px -9px 18px #ffffff;
}

@keyframes opacity {
    from{
        opacity: 0;
        /* margin-left: -15rem; */
    } to{
        opacity: 1;
        /* margin-left: 0; */
    }
}