.home__main{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: #f5f5f5;
}

.home__main .header__main{
    width: 100%;
    display: flex;
    /* justify-content: space-between; */
    flex-direction: row-reverse;
    position: absolute;
    top: 20px;
}

.home__main .container__detail.expands{
    display: flex;
}
.home__main .container__detail{
    position: absolute;
    z-index: 99;
    right: 0;
    display: none;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    flex-direction: row-reverse;
}

.home__main .container__detail .detail{
    width: 420px;
    height: 100%;
    background: #ffffff;
}

.home__main .container__detail .detail-header .title{
    font-weight: 600;
    font-size: 1.1rem;
    letter-spacing: 1px;
    margin-left: 0.8rem;
}

.home__main .container__detail .detail-header .back__:hover{
    transform: translate(-8px);
    cursor: pointer;
}

.home__main .container__detail .detail-header .back__{
    margin-left: 1rem;
    font-size: 1.3rem;
    font-weight: 600;
    transition: .5s;
}

.home__main .container__detail .detail-header{
    display: flex;
    width: 100%;
    padding: 1.3rem 0;
}

.home__main .header__main .info{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 2rem;
    border-radius: 10px;
    transition: .5s;
background: #ffffff;
    box-shadow:  9px 9px 18px #e8e8e8, 
                 -9px -9px 18px #ffffff;
             padding: 0.3rem 0.4rem;
    opacity: 0.9;
    color: rgba(0, 0, 0, 0.788);
}

.container__title .title{
    font-weight: 700;
    font-size: 2.2rem;
    letter-spacing: 2px;
}

.container__title .sub{
    margin-top: 5px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.5);
    text-align: center;
    font-size: 1.1rem;
}

.container__title .sub span{
    color: rgba(0, 0, 0);
    letter-spacing: 1px;
}

.container__phone{
    width: 320px;
  height: 650px;
  background-color: #ffffff;
  position: relative;
  margin: 50px auto;
  border: 6px solid #bdbbbb;
  border-radius: 50px;
  box-shadow: 14px 21px 42px 10px rgba(0, 0, 0, .2);
}

.iphone {
    width: 292px;
    height: 622px;
    background-color: #ffffff;
    border: 16px solid #202020;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 47px;
}

.iphone::before{
    position: absolute;
    content: "";
    margin-top: 7rem;
    width: 6px;
    height: 80px;
    right: -23px;
    background: #bdbbbb;
}
.iphone::after{
    position: absolute;
    content: "";
    margin-top: -560px;
    width: 6px;
    height: 30px;
    left: -23px;
    background: #bdbbbb;
}
.container__phone::after{
    position: absolute;
    content: "";
    margin-top: 8.2rem;
    width: 6px;
    height: 40px;
    left: -8px;
    background: #bdbbbb;
}
.container__phone::before{
    position: absolute;
    content: "";
    margin-top: 11.6rem;
    width: 6px;
    height: 40px;
    left: -8px;
    background: #bdbbbb;
}

.iphone .header{
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    height: 28px;
    background-color: #f9f9f9; /*linear-gradient(to top left,  #D0FBED,#FDD7EA , #f9f9f9); */
    border-radius: 45px 45px 0 0;
    /* border-bottom: 1px solid #ededed; */
}

.iphone .header .frame__header {
    position: absolute;
    width: 170px;
    height: 30px;
    top: -4px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #202020;
    border-radius: 0 0 23px 23px;
}

.iphone .header .frame__header .speaker{
    margin-top: 12px;
          width: 40px;
          height: 4px;
          background-color: #404040;
          margin-left: auto;
          margin-right: auto;
}

.iphone .header .frame__header .camera{
    position: absolute;
    width: 7px;
    height: 7px;
    right: 40px;
    top: 7.5px;
    background-color: #090A50;
    border: 3px solid #404040;
    border-radius: 50%;
}

.iphone .header .header__bars{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    color: #404040;
}

.iphone .header  .header__bars .header__bars-block{
    margin-top: 3px;
}
.iphone .header  .header__bars .header__bars-block span{
   font-size: 12px;
   margin-left: 20px;
   font-weight: 700;
   letter-spacing: 1px;
}

.iphone .header  .header__bars .header__bars-block .MuiSvgIcon-root{
    font-size: 13px;
    opacity: 0.6;
    margin-right: 3px;
    color: #202020;
}

.iphone .header  .header__bars .header__bars-block .MuiSvgIcon-root:last-child{
    transform: rotate(90deg);
    margin-right: 12px;
}

.container__phone .iphone .body{
    width: 100%;
    height: 558px;
    /* height: 100%; */
    /* background: #404040; */
    /* border-radius: 0 0 30px 30px; */
    /* overflow-y: auto;  */
    margin-bottom: 6px;
}

.container__phone .iphone .body-scroll{
    width: 100%;
    height: 558px;
    /* height: 100%; */
    /* background: #404040; */
    /* border-radius: 0 0 30px 30px; */
    overflow-y: auto; 
    margin-bottom: 6px;
}

.container__phone .iphone .footer{
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 0 30px 30px;
    background-color: #f9f9f9;
}

.container__phone .iphone .footer .frame__footer{
    width: 100px;
    height: 4px;
    border-radius: 10px;
    opacity: 0.5;
    background: #747474;
}

.home__main .footer__container{
    width: 100%;
    position: absolute;
    bottom: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.home__main .footer__container .ig__{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 2rem;
    border-radius: 10px;
    transition: .5s;
background: #ffffff;
    box-shadow:  9px 9px 18px #e8e8e8, 
                 -9px -9px 18px #ffffff;
             padding: 0.3rem 0.4rem;
             /* font-size: 3rem; */
    opacity: 0.9;
    color: rgba(0, 0, 0, 0.788);
}
       
.home__main .footer__container .buy__me {
    text-decoration: none;
    transition: .5s;
    display: flex;
    color: rgba(0, 0, 0, 0.788);
    align-items: center;
    border-radius: 10px;
    background: #ffffff;
    box-shadow:  9px 9px 18px #e8e8e8, 
                 -9px -9px 18px #ffffff;
                 padding: 0.8rem;
             margin-right: 2rem;
}

.home__main .footer__container .buy__me:hover,
.home__main .footer__container .ig__:hover,
.home__main .header__main .info:hover{
    cursor: pointer;
    transform: scale(1.1);
}

.home__main .footer__container .buy__me h5{
    font-size: 1.1rem;
    /* letter-spacing: 1px; */
    font-weight: 500;
}

.home__main .footer__container .buy__me img{
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.5rem;
}

.home__main .footer__container .ig__ .custom{
    width: 2.2rem;
    height: 2.2rem;
}

::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background:transparent;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(185, 185, 185);
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(185, 185, 185);
  }

  @media only screen and (max-width: 540px) {
    .home__main .header__main,
    .home__main .container__title,
    .home__main .footer__container,
    .home__main .iphone .footer{
        display: none;
    }

    .container__phone .iphone .body-scroll {
        height: 100vh;
    }

    .card__list .card__heading .color__heading{
        width: 40px;
        height: 40px;
    }

    .card__list .card__heading .card__expands .MuiSvgIcon-root{
        font-size: 1.9rem;
    }

    .video__container,
    #video-tracking{
        width: 100%;
        height: 60vh;
    }
    
    #camera{
        width: 70px;
        height: 70px;
    }

    .btn__shot{
        width: 110px;
        height: 110px;
    }

    .btn__flip{
        width: 80px;
        height: 80px;
    }

     .marker{
        margin-top: -3rem;
    }

    .main__container{
        height: 100vh;
    }

    .home__main .container__phone,
    .container__phone .iphone .body,
    .home__main .container__phone .iphone{
        width: 100%;
        height: 100%;
        margin: 0;
        border: none;
        border-radius: 0;
    }

    .bottom__content{
        bottom: 0;
        position: relative;
        height: 30vh;
    }

    .icon__list .list-icon .MuiSvgIcon-root{
        font-size: 1.9rem;
    }
    
    .home__main .container__phone::before,
    .home__main .container__phone::after,
    .home__main .container__phone .iphone::after,.home__main .container__phone .iphone::before,
    .home__main .container__phone .header{
        display: none;
    }

  }