    color: rgb(255,255,255);
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    /* box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23); */
}

.shake{
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97);
}

@keyframes shake {
    10%, 90% {
      transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(4px, 0, 0);
    }
  }
  
.video__container {
    /* width: 100%; */
    width: 292px;
    height: 350px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: white;
    overflow: hidden;
}

#video-tracking {
    z-index: -2;
    width: 100%;
    /* width: 292px; */
    height: 350px;
    position: absolute;
}

.main__content{
    width: 100%;
    height: 100%;
