}

.hidden__canvas {
    /* object-fit: fill; */
    z-index: 3;
    /* display: none; */
    /* position: absolute; */
    /* width: 100%; */
    /* width: 292px; */
    height: 350px;
}

.circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    z-index: 5;
    background: rgb(255, 255, 255);
}

.circle__color {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    z-index: 1;
    background: rgb(255, 255, 255);
}

.circle-container {
    position: absolute;
    /* width: 15em; */
    z-index: 5;
    /* height: 15em; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.circle__item {
    opacity: 0;
    border-radius: 50%;
    background-color: deepskyblue;
    width: 4em;
    height: 4em;
    position: absolute;
    animation: scaleIn 4s infinite cubic-bezier(.36, .11, .89, .32);
}

@keyframes scaleIn {
    from {
        transform: scale(.01, .01);
        opacity: .5;
    }
    to {
        transform: scale(3.5, 3.5);
        opacity: 0;
    }
}

.hover__item {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    z-index: 5;
    background-color: rgba(241, 237, 237, 0.747);
    padding: 10px;
    border-radius: 1rem;
    margin-top: -6em;
    /* margin-top: -3em; */
}

.marker {
    transform: perspective(40px) rotateX(20deg) rotateZ(-45deg);
    border-radius: 50% 50% 50% 0;
    padding: 1px;
    width: 50px;
    height: 50px;
    background-color: rgba(241, 237, 237, 0.747);
    position: absolute;
}

.hover__item h5 {
    font-size: 1.25em;
}

.bottom__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    bottom: 0;
    margin-bottom: 2rem;
    /* height: 294px; */
}

