.container__phone{
    width: 320px;
  height: 650px;
  background-color: #ffffff;
  position: relative;
  margin: 50px auto;
  border: 6px solid #bdbbbb;
  border-radius: 50px;
  box-shadow: 14px 21px 42px 10px rgba(0, 0, 0, .2);
}

.iphone {
    width: 292px;
    height: 622px;
    background-color: #ffffff;
    border: 16px solid #202020;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 47px;
}

.iphone::before{
    position: absolute;
    content: "";
    margin-top: 7rem;
    width: 6px;
    height: 80px;
    right: -23px;
    background: #bdbbbb;
}
.iphone::after{
    position: absolute;
    content: "";
    margin-top: -560px;
    width: 6px;
    height: 30px;
    left: -23px;
    background: #bdbbbb;
}
.container__phone::after{
    position: absolute;
    content: "";
    margin-top: 8.2rem;
    width: 6px;
    height: 40px;
    left: -8px;
    background: #bdbbbb;
}
.container__phone::before{
    position: absolute;
    content: "";
    margin-top: 11.6rem;
    width: 6px;
    height: 40px;
    left: -8px;
    background: #bdbbbb;
}

.iphone .header{
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    height: 28px;
    background-color: #f9f9f9; /*linear-gradient(to top left,  #D0FBED,#FDD7EA , #f9f9f9); */
    border-radius: 45px 45px 0 0;
    /* border-bottom: 1px solid #ededed; */
}

.iphone .header .frame__header {
    position: absolute;
    width: 170px;
    height: 30px;
    top: -4px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #202020;
    border-radius: 0 0 23px 23px;
}

.iphone .header .frame__header .speaker{
    margin-top: 12px;
          width: 40px;
          height: 4px;
          background-color: #404040;
          margin-left: auto;
          margin-right: auto;
}

.iphone .header .frame__header .camera{
    position: absolute;
    width: 7px;
    height: 7px;
    right: 40px;
    top: 7.5px;
    background-color: #090A50;
    border: 3px solid #404040;
    border-radius: 50%;
}

.iphone .header .header__bars{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    color: #404040;
}

.iphone .header  .header__bars .header__bars-block{
    margin-top: 3px;
}
.iphone .header  .header__bars .header__bars-block span{
   font-size: 12px;
   margin-left: 20px;
   font-weight: 700;
   letter-spacing: 1px;
}

.iphone .header  .header__bars .header__bars-block .MuiSvgIcon-root{
    font-size: 13px;
    opacity: 0.6;
    margin-right: 3px;
    color: #202020;
}

.iphone .header  .header__bars .header__bars-block .MuiSvgIcon-root:last-child{
    transform: rotate(90deg);
    margin-right: 12px;
}

